.container {
  height: 100%;
}

.container-fluid {
  height: 100vh;
  min-height: 100vh;
}

.img-wrapper {
  width: 300px;
  height: 300px;
  cursor: pointer;
  display: flex;
  justify-content: center;
}

.img {
  width: 100%;
  height: 100%;
}

.loading-wrapper {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50%;
}

.card-columns {
  margin-bottom: 10px !important;
}

.error {
  height: 100%;
  justify-content: center;
  align-items: center;
}
